<template>
    <div class="exchange-record">
        <div class="content-top">
            <div class="right">
                <span>{{ $L('时间') }}：</span>
                <el-date-picker class="time-search"
                                style="width: 350px;"
                                v-model="SearchTimeZone"
                                type="datetimerange"
                                align="right"
                                v-bind:size="btnSize"
                                :start-placeholder="$L('开始日期')"
                                :end-placeholder="$L('结束日期')"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                :default-time="['00:00:00', '23:59:59']"
                                @change="SearchTimeFunc">
                </el-date-picker>
                <el-select class="search" v-model="type" v-bind:size="btnSize"
                           @change="changeTypeFunc">
                    <el-option
                            v-for="item in messageTypelist"
                            :key="item.type"
                            :label="item.name"
                            :value="item.type">
                    </el-option>
                </el-select>
                <el-input :placeholder="$L('输入标题')" v-bind:size="btnSize" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="exchange-list">
            <el-table
                    :data="tableData.list"
                    stripe
                    :empty-text="$L('暂无数据')"
                    height="600"
                    :header-cell-style="{color:'#666'}"
                    style="width: 100%" class="table">
                <el-table-column
                        prop="info"
                        :label="$L('兑换信息')"
                        align="left" width="500">
                    <div slot-scope="scope" class="info">
                        <img :src="scope.row.img" alt="" class="img">
                        <div class="textBox">
                            <p>{{ scope.row.name }}</p>
                        </div>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        :label="$L('兑换时间')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="points"
                        :label="$L('消费积分')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="state"
                        :label="$L('类型')"
                        align="center">
                    <div slot-scope="scope">
                        <p>{{ scope.row.typeName }}</p>
                    </div>
                </el-table-column>
<!--                <el-table-column-->
<!--                        prop="handle"-->
<!--                        :label="$L('操作')"-->
<!--                        align="center">-->
<!--                    <el-button type="primary">申请领奖</el-button>-->
<!--                    <p class="details">订单详情</p>-->
<!--                </el-table-column>-->
            </el-table>
        </div>
        <!-- 分页 -->
        <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>pageSize">
            <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="pageSize"
                    layout="prev, pager, next, total, jumper"
                    :total="tableData.allNum">
            </el-pagination>
        </div>
    </div>

</template>

<script>
    export default {
        name: "exchangeRecord",
        data () {
            return {

                btnSize:'small',
                inputValue:'',
                SearchTimeZone:'',
                type:'all',
                pageSize:20,
                currentPage:1,
                dtCreateStart:'',
                dtCreateEnd:'',
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部类型')
                    },
                    {
                        type:'prod',
                        name:this.$L('产品')
                    },
                    {
                        type:'gift',
                        name:this.$L('礼品')
                    },
                    {
                        type:'coupon',
                        name:this.$L('优惠券')
                    },
                ],
                tableData:{},

            }
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    type : _self.type,
                    keyword : _self.inputValue,
                    dtCreateStart : _self.dtCreateStart,
                    dtCreateEnd : _self.dtCreateEnd,
                };
                _self.$api('getPointsStoreRecordList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'integralShop'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'integralShop'});
                    });
                });
            },
            SearchTimeFunc(time){
                // dtCreateStart   dtCreateEnd
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val){
                this.type = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            stateTypeFunc(i){
                let state;
                switch(i){
                    case 0:
                        state = {
                            text:'产品'
                        };
                        break;
                    case 1:
                        state = {
                            text:'礼品',
                        };
                        break;
                    case 2:
                        state = {
                            text:'优惠券'
                        };
                        break;
                    default:
                        state = {
                            text:'未知报错',
                            color:'red'
                        };
                        break;
                }
                return state;
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    .exchange-record{
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        padding: 20px;
        .content-top{
            height: 32px;
            margin-bottom: 10px;
            .left{
                float: left;
            }
            .right{
                float: right;
                .time-search{
                    vertical-align: bottom;
                }
                .font{
                    padding-left: 20px;
                    font-size: 16px;
                }
                .search{
                    width: 120px;
                    padding-left: 10px;
                }
                .input-with-select{
                    width: 280px;
                    padding-left: 10px;
                }
            }
        }
        .exchange-list{
            background: #fff;
            padding: 0 20px;
            .table{
                .info{
                    height: 100px;
                    img{
                        width: 100px;
                        height: inherit;
                        display: inline-block;

                    }
                    .textBox{
                        width: 60%;
                        padding: 5px 0 0 20px;
                        display: inline-block;
                        vertical-align: top;
                        p:first-child{
                            font-size: 14px;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                        }
                    }
                }

                .red{
                    color: #FF2D52;
                }
                .green{
                    color: #00CC34;
                }
                .details{
                    margin-top: 10px;
                }
            }
        }
        .list-pages{
            background: #fff;
            text-align: center;
            padding: 40px 0;
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>